body {
  margin: 0;
  padding-top: 60px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("img/Castle.jpg");
  background-color: #cccccc;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.App {
  text-align: center;
}

.content {
  margin-top: 20px;
}

.navbar {
  background-color: rgba(10, 0, 0, 0.6);
  color: white;
}

.Tavern {
  border-color: rgba(0, 30, 30, 0.6);
  border-width: 3px;
}

.TavernTimer {
  color: darkred;
  font-size: 22px;
}

.TavernStats{
  color: chocolate;
  font-size: 14px;
  width: 440px;
  background-color: rgba(22, 17, 12, 0.3);
  border-radius: 15px;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.TavernAcceptingButtons {
  background-color: rgba(10, 0, 0, 0.4);
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
  margin-top: 40px;
  margin-bottom: 20px;
}

.Units {
  background-color: rgba(10, 0, 0, 0.6);
  color: white;
  padding: 20px;
  border-radius: 5px;
  margin-bottom: 20px;
  min-height: 300px;
}

.HeroFull {
  max-width: 1000px;
  min-height: 700px;
  color: rgba(0, 30, 30, 0.6);
}

.Hero {
  position: relative;
}

.level {
  position: absolute;
  height: 50px;
  width: 30px;
  top: 3px;
  left: 7px;
  color: inherit;
  background-color: inherit;
}

.triangle-topleft {
  top: 53px;
  left: 7px;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 15px solid;
  border-top-color: inherit;
  border-right: 15px solid transparent;
}

.triangle-topright {
  top: 53px;
  left: 22px;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 15px solid;
  border-top-color: inherit;
  border-left: 15px solid transparent;
}

.HeroPicture {
  position: relative;
  margin: 10px;
  border-width: 3px;
  border-style: solid;
}

.level2 {
  position: absolute;
  height: 70px;
  width: 50px;
  top: 0px;
  left: 7px;
  color: inherit;
  background-color: inherit;
  font-size: 22px;
}

.triangle-topleft2 {
  top: 70px;
  left: 7px;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 25px solid;
  border-top-color: inherit;
  border-right: 25px solid transparent;
}

.triangle-topright2 {
  top: 70px;
  left: 32px;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 25px solid;
  border-top-color: inherit;
  border-left: 25px solid transparent;
}

.Rarity0{
  background-color: aliceblue;
  border-top-color: aliceblue;
  color: chocolate;
}
.Rarity1{
  background-color: gainsboro;
  border-top-color: gainsboro;
  color: chocolate;
}
.Rarity2{
  background-color: burlywood;
  border-top-color: burlywood;
  color: chocolate;
}
.Rarity3{
  background-color: seagreen;
  border-top-color: seagreen;
  color: bisque;
}
.Rarity4{
  background-color: gold;
  border-top-color: gold;
  color: chocolate;
}
.Rarity5{
  background-color: crimson;
  border-top-color: crimson;
  color: bisque;
}

.RarityBorder0{
  border-color: aliceblue;
}
.RarityBorder1{
  border-color: gainsboro;
}
.RarityBorder2{
  border-color: burlywood;
}
.RarityBorder3{
  border-color: seagreen;
}
.RarityBorder4{
  border-color: gold;
}
.RarityBorder5{
  border-color: crimson;
}

.ClassImg {
  position: absolute;
  height: 30px;
  width: 30px;
  bottom: 12px;
  right: 12px;
}

.HeroStats {
  background-color: rgba(184, 139, 103, 0.719);
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 15px;
  margin: 20px 0;
  min-width: 440px;
}

.CreatureStats {
  position: absolute;
  width: 360px;
  bottom: 5px;
  z-index: 10;
  left:0;
  right:0;
  padding: 3px 0 3px 8px;
  background-color: rgba(22, 17, 12, 0.9);
  border-radius: 15px;
  margin-left: auto;
  margin-right: auto;
}

.FightResult {
  position: absolute;
  width: 200px;
  top: 100px;
  left:0;
  right:0;
  padding: 8px;
  color: red;
  background-color: rgba(10, 0, 0, 0.7);
  font-size: 22px;
  border-radius: 15px;
  margin-left: auto;
  margin-right: auto;
  border-color: rgba(3, 206, 206, 0.733);
  border-width: 3px;
  border-style: solid;
}

.CreatureStat {
  padding: 0;
  margin: 0;
}

.HeroProgress {
  position: relative;
  overflow: hidden;
  background-color: rgba(184, 139, 103, 0.719);
  border-radius: 15px;
  margin: 10px 0;
  height: 32px;
}

.ProgressFill {
  position: absolute;
  background-color: rgba(105, 78, 55, 0.719);
  height: 32px;
}

.ProgressText {
  color: cyan;
  font-size: 20px;
  font-weight: 600;
  z-index: 10;
}

.Creatures {
  position: relative;
  margin: 10px 40px 10px 40px;
  border-color: rgba(3, 206, 206, 0.733);
  border-width: 3px;
  border-style: solid;
}

.creature-exp{
  position: absolute;
  padding: 8px;
  top: 10px;
  right: 7px;
  color: rgba(3, 206, 206, 0.733);
  background-color: rgba(10, 0, 0, 0.4);
  font-size: 22px;
  border-color: rgba(3, 206, 206, 0.733);
  border-width: 3px;
  border-style: solid;
}

.StatFlex {
  flex-basis: auto;
  flex-grow: 1;
}

.Stat {
  width: 52px;
  margin: auto;
}

.StatText {
  display: inline-block;
  border-radius: 10px;
  background-color: rgba(10, 0, 0, 0.4);
  padding: 0 5px;
}

.StatMax {
  border-radius: 10px;
  background-color: rgba(10, 0, 0, 0.4);
  color: red;
}

.HeroStats img{
  width: 50px;
  margin-bottom: 3px;
}

.SetOrder {
  border-color: rgba(175, 108, 6, 0.925);
  border-width: 3px;
  border-style: solid;
  margin: 10px;
  padding: 10px;
}

.card {
  background-color: rgba(255, 255, 255, 0.5);
  margin-bottom: 20px;
}

.BorderImg {
  border-width: 3px;
  border-style: solid;
}

.BorderImg:hover {
  border-color: rgba(107, 1, 89, 0.6);
  border-width: 3px;
  border-style: solid;
}

.BottomInfo {
  color: white;
}

footer {
  padding-top: 10px;
  background-color: rgba(10, 0, 0, 0.6);
  color: white;
  height: 70px;
}

.modal-open {
	overflow-y: auto;
	padding-left: 17px;
}

.modal-footer {
  border-color: rgba(175, 108, 6, 0.925);
}

.modal-header {
  border-color: rgba(175, 108, 6, 0.925);
}

.modal-header input{
  width: 100%;
  border-color: rgba(175, 108, 6, 0.925);
  color: rgba(175, 108, 6, 0.925);
  border-width: 3px;
  border-style: solid;
  border-radius: 5px;
  font-size: 22px;
  background-color: rgba(10, 0, 0, 0.2);
}

.HeroHeader {
  font-size: 14px;
}

.nav-tabs {
  border-color: rgba(175, 108, 6, 0.925);
}

.nav-tabs .nav-link.active, .nav-tabs .nav-link:hover {
  border-color: rgba(175, 108, 6, 0.925);
  color: rgba(175, 108, 6, 0.925);
  background-color: rgba(10, 0, 0, 0.8);
}

.Units .nav-tabs {
  border: none;
}

.Units .nav-tabs .nav-link{
  color: rgba(175, 108, 6, 0.925);
  background-color: rgba(10, 0, 0, 0.8);
}

.spin {
	position: fixed;
	z-index: 100;
	height: 4rem;
	width: 4rem;
	top: 350px;
	left: 50%;
	margin-left: -2rem;
}

.modal-content {
	text-align: center;
	background-color: rgba(10, 0, 0, 0.8);
	color: rgba(175, 108, 6, 0.925);
}


.nav-link {
  border-left: 1px solid rgba(255, 255, 255, 0.1);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}


